import React from 'react';
import hen from './256hen.png';
import me from "./256me.png";
import './Home.css';

class GoButton extends React.Component {
  render() {
    return(
      <button
        id = "clickable"
        className = { this.props.className }
        onClick = { this.props.onClick }
      >
        { this.props.text }
      </button>

    );
  }
}

