import React from "react";
import routes from "./routes.json";

class Ribbon extends React.PureComponent {
  constructor( props ) {
    super( props );
    this.state = {};
  }
  render() {
    return(
      <header>
        <nav>
          <span id = "clickable"
            onClick = { () => this.props.changePage( routes.base ) }
          >
            Connor Bessell
          </span>
          &nbsp;
          <li onClick = { () => this.props.changePage( routes.base ) } >
            Home
          </li>
          <li onClick = { () => this.props.changePage( routes.base + routes.portfolio ) } >
            Portfolio
          </li>
          <li onClick = { () => this.props.changePage( routes.base + routes.blog ) } >
            Blog
          </li>
          <li onClick = { () => this.props.changePage( routes.base + routes.demos.base ) } >
            Demos
          </li>
        </nav>
      </header>
    );
  }
}

export default Ribbon;
