import { Route, Switch, Redirect, BrowserRouter } from "react-router-dom";
import routes from "./routes.json";
import React from "react";
import { createHashHistory } from "history";
import Home from "./Home.js";
import Portfolio from "./Portfolio.js";
import Blog from "./Blog.js";
import Demos from "./Demos/Demos.js";
import LoginPage from "./Demos/authSystem/Login.js";
import ProtectedPage from "./Demos/authSystem/Protected.js";
import Ribbon from "./Ribbon.js";
import YYYYYY from "./Demos/games/YYYYYY.js";

const Router = () => {

  let history = createHashHistory({
    forceRefresh: true,
  });

  // TODO: try that useRouteMatch() hook & see if we can nest the demos/... into demos.js

  let authState = false;
  try {
    authState = !!history.location.state.auth;
  } catch( e ) {
    console.info( "no auth" );
  }

  const setAuth = ( auth ) => {
    if( auth === true ) {
      authState = auth;
      history.push( "/demos/protected", { auth: auth } );
    }
    else {
      authState = false;
      history.push( "/demos/login", { auth: false } );
    }
  }

  const changePage = ( to ) => {

    console.debug( `navigating to: ${to}` );
    history.push( to );

  }

  return(
    <BrowserRouter basename = { "#" } >
      <Ribbon changePage = { ( to ) => changePage( to ) } />
      <Switch>
        <Route exact path = { routes.base } >
          <Home changePage = { ( to ) => changePage( to ) } />
        </Route>
        <Route exact path = { routes.base + routes.portfolio } >
          <Portfolio changePage = { ( to ) => changePage( to ) } />
        </Route>
        <Route exact path = { routes.base + routes.blog } >
          <Blog changePage = { ( to ) => changePage( to ) } />
        </Route>
        <Route exact path = { routes.base + routes.demos.base } >
          <Demos changePage = { ( to ) => changePage( to ) } />
        </Route>

        <Route exact path = { routes.base + routes.demos.base + routes.demos.YYYYYY } >
          <YYYYYY/>
        </Route>
        <Route exact path = { routes.base + routes.demos.base + routes.demos.login } >
          <LoginPage
            setAuth = { ( auth ) => setAuth( auth ) }
            changePage = { ( to ) => changePage( to ) }
          /> 
        </Route>
        <Route exact path = { routes.base + routes.demos.base + routes.demos.protected }
          render = { ( props ) =>
            authState ? ( 
              <ProtectedPage
                setAuth = { ( auth ) => setAuth( auth ) }
                changePage = { ( to ) => changePage( to ) }
              /> 
              ) : ( 
              <Redirect
                to = {{
                  pathname: routes.base + routes.demos.base + routes.demos.login,
                  state: {
                    from: props.location
                  }
                }}
              /> 
            )
          }
        />

        <Route path = "*">
          <h3>404 BruH This page don't exist</h3>
          <button onClick = { () => changePage( routes.base ) } >Eject</button>
        </Route>
      </Switch>
    </BrowserRouter>
  );

};

export default Router;

